.AnimeSlider_小ontainer{position:relative;width:100%;max-width:1300px;margin:0 auto;overflow:hidden;display:flex;align-items:center}.AnimeSlider_小ontainer:hover button{background-color:#333;color:#fff}.AnimeSlider_小ontainer button{position:absolute;height:50px;width:30px;background-color:rgba(51,51,51,0);border:none;color:rgba(255,255,255,0);cursor:pointer;z-index:3}.AnimeSlider_小ontainer button:hover{background-color:#999;color:#fff}.Slider_Arrow-left{left:0}.Slider_Arrow-right{right:0}.AnimeSlider{display:flex;transition:transform .5s ease;overflow-x:auto;overflow-y:hidden;scroll-behavior:smooth;scrollbar-width:none}.AnimeSeason-Header{display:flex;justify-content:center;align-items:center;width:1300px;padding:13px 1px;background-color:#1a1a1a;border-bottom:3px solid #f66;border-radius:6px 6px 0 0;color:#fff}.App_AnimeSeason{margin-top:20px}